<template>
  <div class="add-owner-box">
    <gs-button
      :disabled="disabled"
      type="primary"
      @click="showModal"
    >配置数据Owner</gs-button>
    <gs-modal
      v-model="visible"
      :width="800"
      :top="86"
      class="add-owner-modal"
      title="配置数据Owner"
    >
      <div class="selected-table">
        <div class="title-text">所选数据表</div>
        <el-table
          :data="tables"
          style="width: 100%"
          max-height="160"
        >
          <el-table-column
            type="index"
            label="序号"
          />
          <el-table-column
            prop="tableName"
            label="表名称"
          />
          <el-table-column
            prop="path"
            label="路径"
          />
        </el-table>
      </div>
      <div class="owners-select">
        <div class="title-text">选择Owner</div>
        <!-- tree-select有问题，先采用hack方式解决 -->
        <gs-tree-select
          v-if="showSelect"
          v-model="owners"
          :tree-props="treeProp"
          :props="keymap"
          :popover-props="popProp"
          width="300px"
          placeholder="请选择Owner"
        />
        <div
          v-if="!showSelect"
          class="loading-text"
        >组织架构数据加载中...</div>
        <div class="tip-text">
          注：数据表Owner作为数据管理员，负责审批使用该表数据的申请。
        </div>
      </div>
      <template slot="footer">
        <gs-button
          :loading="isCreating"
          type="primary"
          @click="handleConfirm"
        >确定</gs-button>
        <gs-button
          :disabled="isCreating"
          @click="close"
        >取消</gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';
import './style';

import {
  list2tree
} from 'src/utils';
export default {
  components: {
  },
  props: {
    tables: {
      type: Array,
      default: () => {
        return [];
      }
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      visible: false,
      keyword: '',
      keymap: {
        label: 'name',
        value: 'realId',
        children: 'children'
      },
      popProp: {
        popperClass: 'owner-select-pop'
      },
      owners: [],
      showSelect: false
    };
  },

  computed: {
    ...mapState('relation', {
      organizations: 'organizations',
      setOwnersStatus: 'setOwnersStatus'
    }),
    treeProp() {
      const {
        organizations
      } = this;
      (organizations.list || []).forEach(item => {
        item.realId = item.id;
        item.realpId = item.pId;
      });
      const treeData = list2tree(organizations.list || [], 'realId', 'realpId');
      return {
        data: treeData,
        showTips: true,
        expandOnFilterMatch: true,
        filterNodeMethod: function (val, data) {
          if (!val) {
            return true;
          }
          return data.name.includes(val);
        }
      };
    },
    isCreating() {
      return checkLoading(this.setOwnersStatus);
    }
  },

  watch: {
    visible(val) {
      this.keyword = '';
      this.owners = [];
    }
  },

  methods: {
    ...mapActions('relation', [
      'getOrganization',
      'setOwners'
    ]),

    showModal() {
      if (!this.tables || !this.tables.length) {
        this.$Message.warning('请选择要配置数据Owner的表');
        return;
      }
      this.visible = true;
      this.getOrganization().then(res => {
        this.showSelect = true;
      }).catch(() => {
        this.showSelect = true;
      });
    },

    handleConfirm() {
      const {
        owners,
        tables
      } = this;
      if (!owners.length) {
        this.$Message.warning('请选择Owner');
        return;
      }
      const ids = tables.map(item => { return item.tableGuid; });
      const arr = owners.map(item => {
        const owner = (this.organizations.list || []).find(i => i.id === item);
        if (owner) {
          return {
            id: owner.id,
            type: owner.type,
            pkid: owner.pkid
          };
        } else {
          return {};
        }
      });
      this.setOwners({tables: ids, owners: arr}).then(res => {
        this.$Message.success('设置Owner成功');
        this.$emit('create-success');
        this.close();
      });
    },

    close() {
      this.visible = false;
      this.showSelect = false;
    }
  }
};
</script>



